/* styles/globals.css */

/* Google Font */
@import url("https://fonts.googleapis.com/css2?family=Kurale&display=swap");

/* From Public Directory */
@font-face {
  font-family: "Kurale";
  src: url("/fonts/Kurale.ttf");
  font-style: medium;
  font-weight: normal;
  font-display: swap;
}

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  color: #333;

  display: block;
  width: 100%;
  max-width: 1200px;

  margin: 0 auto;
}

a {
  color: #0070f3;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

h1 {
  font-size: 12em;
  color: orangered;
}

.logo {
  font-size: 30;
}
